import { cloneElement } from 'react'

const style = {
  width: '100%',
  maxWidth: 1200,
  marginLeft: 'auto',
  marginRight: 'auto'
}

//扩展了container组件的使用范围
export default ({ children, renderer }) => {
  const newElement = cloneElement(renderer, {
    style, children
  })
  return newElement
}
